/**
 * wordpress dependencies
 */
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';

function Save( props ) {
	const {
		attributes: {},
	} = props;
	const blockProps = useBlockProps.save( {
		className:
			'gutenberghub-tab-collapsible gutenberghub-tab-collapsible-initialized',
	} );
	return (
		<div { ...blockProps }>
			<div className="gutenberghub-tab-collapsible-inner-container">
				<InnerBlocks.Content />
			</div>
		</div>
	);
}
export default Save;
