/**
 * wordpress dependencies
 */
import { useSelect } from '@wordpress/data';
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';
/**
 * internal imports
 */
import classNames from 'classnames';
import { getParentBlock } from '../../utils';

function Edit( props ) {
	const { clientId } = props;

	const blockProps = useBlockProps( {
		className: classNames( 'gutenberghub-tab-collapsible' ),
	} );

	const rootClientId = getParentBlock(
		clientId,
		'ghub/tab-button'
	)?.clientId;
	const parentBlockIndex = useSelect( ( select ) =>
		select( 'core/editor' ).getBlockIndex( rootClientId )
	);
	const block = useSelect( ( select ) =>
		select( 'core/block-editor' ).getBlock( clientId )
	);

	return (
		parentBlockIndex === props.context.activeTabIndex && (
			<div { ...blockProps }>
				<div className="gutenberghub-tab-collapsible-inner-container">
					<InnerBlocks
						renderAppender={
							block.innerBlocks.length > 0
								? InnerBlocks.DefaultBlockAppender
								: InnerBlocks.ButtonBlockAppender
						}
					/>
				</div>
			</div>
		)
	);
}
export default Edit;
